import axios from 'axios'
import Vue from 'vue'
import { notFalseValue } from '../util/inspections'

const env = process.env.NODE_ENV || 'development'
const baseUrl = {
  development: 'https://www.easy-mock.com/mock/59f4231396ce6611b3682632/api/v1',
  production: 'https://www.easy-mock.com/mock/59f4231396ce6611b3682632/api/v1'
}
const baseURL = baseUrl[env]

axios.defaults.baseURL = baseURL;
Vue.prototype.$http = axios



