import Vue from 'vue'
import Router from 'vue-router'
import noAuthRouters from './noAuthRouters'
import Error from '@/components/Error'
import Loading from '@/components/Loading'

Vue.use(Router)

// 路由懒加载 const Find = resolve => require(['@/components/Find'], resolve)
const Test = () => import('@/components/Test')
const Test1 = () => import('@/components/Test1')
const Test2 = () => import('@/components/Test2')

//高级配置没用
const Home = () => ({
  // 需要加载的组件。应当是一个 Promise
  component: import('@/components/Home'),
  // 加载中应当渲染的组件
  loading: Loading,
  // 出错时渲染的组件
  error: Error,
  // 渲染加载中组件前的等待时间。默认：200ms。
  delay: 200,
  // 最长等待时间。超出此时间则渲染错误组件。默认：Infinity
  timeout: 3000
})
const Login = () => import('@/components/Login')
const Index = () => import('@/components/Index')
const Explore = () => import('@/components/Explore')
const Feidian = () => import('@/components/Feidian')
const Profile = () => import('@/components/Profile')
const Search = () => import('@/components/Search')

const router = new Router({
  mode: 'history',
  scrollBehavior(to, from, savedPosition) {
    // console.info(to);
    // console.info(from);
    // console.info(savedPosition);
  },
  routes: [
    {
      path: '/test',
      name: 'test',
      component: Test,
      children: [
        {
          path: 'test1',
          name: 'test1',
          component: Test1
        },
        {
          path: 'test2',
          name: 'test2',
          component: Test2
        },
      ]
    },
    {
      path: '/',
      redirect: '/index'
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/search',
      name: 'search',
      component: Search
    },
    {
      path: '/index',
      name: 'index',
      component: Index,
      // redirect:只有进入/index才会触发
      // beforeEnter:进入/index,/index/home...都会触发,next(/index,/index/home...)会死循环
      redirect(to) {
        let userId = localStorage.getItem("userId");
        if (userId) {
          return "/index/home"
        } else {
          return "/index/explore"
        }
      },
      children: [
        {
          path: 'explore',
          name: 'explore',
          component: Explore
        },
        {
          path: 'home',
          name: 'home',
          component: Home
        },
        {
          path: 'feidian',
          name: 'feidian',
          component: Feidian
        },
        {
          path: 'profile',
          name: 'profile',
          component: Profile
        },
      ]
    }
  ]
})

router.beforeEach((to, from, next) => {
  let path = to.path;
  let userId = localStorage.getItem("userId");
  let noAuth = noAuthRouters.includes(path);
  if (!noAuth && !userId) {
    next("/login");
  } else {
    next();
  }
})

export default router



